import React from "react";
// import useLongPress from "../utils/useLongPress";
import "./StockTable.css";

export default function StockTable({ data }) {
  const { trading, items } = data;
  //const past = new Date(2021, 5, 11);
  //const now = new Date();
  //const diff = now.getTime() - past.getTime();
  //console.log(diff / (1000 * 3600 * 24));

  return (
    <div className="stocktable">
      <div className="table_title">
        <strong>{trading}</strong>
      </div>
      <table>
        <thead>
          <tr>
            <th>종목명</th>
            <th>단가</th>
            <th>수량</th>
            <th>입고일</th>
          </tr>
        </thead>
        <tbody>
          {items.map((item) => {
            const date = new Date(item.inputDate);
            return (
              <tr key={item.name}>
                <td className="item_name">{item.name}</td>
                <td className="item_price">
                  {String(item.price).replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                </td>
                <td className="item_quantity">{item.quantity}</td>
                <td className="item_date">
                  {date.getMonth() + 1 + "/" + date.getDate()}
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
}
