import React, { useEffect, useState } from "react";
import DateFnsUtils from "@date-io/date-fns";
import {
  Button,
  DialogActions,
  FormControl,
  InputLabel,
  makeStyles,
  MenuItem,
  OutlinedInput,
  Select,
} from "@material-ui/core";
import {
  KeyboardDatePicker,
  MuiPickersUtilsProvider,
} from "@material-ui/pickers";
import { db } from "../utils/firebase";

const tradings = [
  {
    key: "namu",
    display: "나무(NAMU)",
  },
  {
    key: "hankook",
    display: "한국투자",
  },
  {
    key: "mirae",
    display: "미래에셋",
  },
  {
    key: "kb",
    display: "KB증권",
  },
  {
    key: "shinhan",
    display: "신한알파",
  },
  {
    key: "samsung",
    display: "삼성증권",
  },
];

const useStyles = makeStyles((theme) => ({
  paper: {
    boxSizing: "border-box",
    position: "absolute",
    width: "80%",
    maxWidth: "600px",
    backgroundColor: theme.palette.background.paper,
    borderRadius: "10px",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 2, 1),
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
  },
  selectEmpty: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(3),
  },
  formTrading: {
    minWidth: 120,
  },
  formInput: {
    width: "100%",
    marginBottom: "10px",
  },
}));
const user = "kisskwon87";
function AddStock({ setOpen }) {
  const classes = useStyles();
  const [tradingIndex, setTradingIndex] = useState(0);
  const [name, setName] = useState("");
  const [price, setPrice] = useState("");
  const [quantity, setQuantity] = useState("");
  const [date, setDate] = useState(new Date());

  const handleChangeTrading = (event) => {
    setTradingIndex(event.target.value);
  };

  const handleNameChange = (event) => {
    setName(event.target.value);
  };
  const handlePriceChange = (event) => {
    setPrice(event.target.value);
  };
  const handleQuantityChange = (event) => {
    setQuantity(event.target.value);
  };
  const handleDateChange = (date) => {
    setDate(date);
  };
  const addItem = (event) => {
    event.preventDefault();
    const trading = tradings[tradingIndex];
  };

  useEffect(() => {
    db.collection("user")
      .doc("kisskwon87")
      .onSnapshot((snapshot) => {
        console.log(snapshot.data());
      });
    return () => {};
  }, []);

  return (
    <div className={classes.paper}>
      <form>
        <FormControl className={classes.formTrading}>
          <InputLabel id="trading-select-label">증권계좌</InputLabel>
          <Select
            labelId="trading-select-label"
            id="trading-select"
            value={tradingIndex}
            onChange={handleChangeTrading}
            className={classes.selectEmpty}
            required={true}
          >
            {tradings.map((trading, index) => (
              <MenuItem key={trading.key} value={index}>
                {trading.display}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
        <FormControl variant="outlined" className={classes.formInput}>
          <InputLabel htmlFor="name-input">종목명</InputLabel>
          <OutlinedInput
            id="name-input"
            value={name}
            onChange={handleNameChange}
            label="Name"
            required={true}
          />
        </FormControl>
        <FormControl variant="outlined" className={classes.formInput}>
          <InputLabel htmlFor="price-input">단가</InputLabel>
          <OutlinedInput
            id="price-input"
            value={price}
            onChange={handlePriceChange}
            label="Price"
            type="number"
            required={true}
          />
        </FormControl>
        <FormControl variant="outlined" className={classes.formInput}>
          <InputLabel htmlFor="quantity-input">수량</InputLabel>
          <OutlinedInput
            id="quantity-input"
            value={quantity}
            onChange={handleQuantityChange}
            label="Quantity"
            type="number"
          />
        </FormControl>
        <MuiPickersUtilsProvider utils={DateFnsUtils}>
          <KeyboardDatePicker
            disableToolbar
            variant="inline"
            format="MM/dd/yyyy"
            margin="normal"
            id="date-picker-inline"
            label="Month/Day/Year"
            value={date}
            onChange={handleDateChange}
            KeyboardButtonProps={{
              "aria-label": "change date",
            }}
            PopoverProps={{
              anchorOrigin: { vertical: "top", horizontal: "right" },
              transformOrigin: { vertical: "bottom", horizontal: "right" },
            }}
            className={classes.formInput}
          />
        </MuiPickersUtilsProvider>
        <DialogActions>
          <Button
            color="primary"
            onClick={() => {
              setOpen(false);
            }}
          >
            취소
          </Button>
          <Button color="primary" onClick={() => {}} type="submit" autoFocus>
            추가
          </Button>
        </DialogActions>
      </form>
    </div>
  );
}

export default AddStock;
