import React from "react";
import { Modal, Fab, makeStyles } from "@material-ui/core";
import AddIcon from "@material-ui/icons/Add";
import StockTable from "../components/StockTable";
import "./Main.css";
import AddStock from "../components/AddStock";

const datas = {
  namu: {
    trading: "나무(NAMU)",
    items: [
      {
        name: "씨앤씨인터네셔널",
        price: 105000,
        quantity: 1,
        inputDate: 1620658800000,
      },
    ],
  },
};

const useStyles = makeStyles((theme) => ({
  fav: {
    position: "fixed",
    bottom: 0,
    left: "50%",
    transform: "translateX(-50%)",
    marginBottom: "30px",
  },
}));

function Main() {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  return (
    <>
      <div className="main">
        <StockTable data={datas.namu} />
      </div>
      <Fab
        className={classes.fav}
        color="primary"
        aria-label="add"
        onClick={handleOpen}
      >
        <AddIcon />
      </Fab>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="add-stock"
        aria-describedby="add-stock"
      >
        <div>
          <AddStock setOpen={setOpen} />
        </div>
      </Modal>
    </>
  );
}

export default Main;
