import React from "react";
import { useHistory } from "react-router-dom";

function Login() {
  const history = useHistory();
  const onClickLogin = () => {
    history.push("/main");
  };
  return (
    <div style={{ textAlign: "center" }}>
      <button onClick={onClickLogin}>Login</button>
    </div>
  );
}

export default Login;
